.navbar {
    display: flex;
    justify-content:center; /* Coloca a logo e a barra de pesquisa de um lado e o menu do outro */
    align-items: center;
    background: linear-gradient(90deg, #f7e3b4, #d4a373);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
    padding: 15px 40px;
    width: 100%;
}

.navbarContent {
    display: flex;
    align-items: center; 
    gap: 80px; 
}

.logo {
    width: 80%;
    max-width: 120px;
}

.list {
    display: flex;
    list-style: none;
    align-items: center;
    margin: 0;
}

.item {
    flex-grow: 1;
    text-align: center;
}

.item a {
    color: #333;
    text-decoration: none;
    display: block;
    padding: 10px 30px;
    border-radius: 5px;
    transition: background 0.3s, color 0.3s;
}

.item a:hover {
    background-color: rgba(0, 0, 0, 0.1);
    color: #000;
}
