/** @format */

.respostasLista {
	margin-top: 10px;
	/* padding-left: 50px; */
	display: flex;
	flex-direction: column;
	gap: 20px;
}

.respostaItem {
	display: flex;
	align-items: flex-start;
	margin-top: 5px;
}

.respostaItem img {
	width: 30px;
	height: 30px;
	border-radius: 50%;
	margin-right: 8px;
}

.resposta-texto-container {
	display: flex;
	align-items: center; /* Alinha o texto e o botão */
}

.respostaTexto {
	font-size: 13px;
	color: #555;
	margin: 0;
}

.respostaUsuario {
	font-weight: bold;
	color: #333;
	font-size: 14px;
}

.respostaContent {
	display: flex;
	flex-direction: column;
	gap: 3px;
}
